*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    user-select: none;
    user-zoom: none;
}
.screen{
    position: relative;
    width:100%;
    height:100vh;
    justify-content: flex-start;
}
.screen .profile{
    width:300px;
    height:100%;
    position: relative;
}